<template>
  <div>
    <el-button class="back" @click="$router.back()">返回列表</el-button>
    <ul class="nav">
      <li
        v-for="(item,index) in floor_list"
        :class="{con:num==index}"
        :key="item.type"
        @click="checkout(index)"
      >{{item.type}}</li>
    </ul>
    <div>
      <div class="floor" v-for="item in floor_list" :key="item.type" ref="floor">
        <h2 class="floor_title">{{item.type}}</h2>
        <list :list="item.list"></list>
      </div>
    </div>
  </div>
</template>
<script>
import $ajax from "../plugins/ajax";
import scrollTo from "../plugins/scrollt";
export default {
  beforeRouteEnter: async function(to, from, next) {
    var res = await $ajax("/goodList?type_one=" + to.params.type);
    next(vm => {
      vm.list = res.data;
    });
  },
  beforeRouteUpdate: async function(to, from, next) {
    var res = await $ajax("/goodList?type_one=" + to.params.type);
    this.list = res.data;
    next();
  },
  data() {
    return {
      list: [],
      num: "",
      flag2: true
    };
  },
  computed: {
    floor_list() {
      var floor_list = []; //[{type:"",list:[{},{}]}]
      for (var i = 0; i < this.list.length; i++) {
        var flag = true;
        for (var j = 0; j < floor_list.length; j++) {
          if (this.list[i].type_two == floor_list[j].type) {
            floor_list[j].list.push(this.list[i]);
            flag = false;
            break;
          }
        }
        if (flag) {
          floor_list.push({
            type: this.list[i].type_two,
            list: [this.list[i]]
          });
        }
      }

      return floor_list;
    }
  },
  created() {
    this.winscroll();
  },
  methods: {
    checkout(index) {
      this.flag2 = false;
      this.num = index;
      var title = this.$refs.floor[index];
      scrollTo(title.offsetTop - 70, () => {
        this.flag2 = true;
      });
    },
    winscroll() {
      window.addEventListener("scroll", this.refscroll.bind(this));
    },
    refscroll() {
      var st = document.body.scrollTop || document.documentElement.scrollTop;
      var H =
        window.innerHeight ||
        document.body.clientHeight ||
        document.documentElement.clientHeight;
      var content = this.$refs.floor;
      for (var k = 0; k < content.length; k++) {
        var ot = content[k].offsetTop;

        var h = parseFloat(getComputedStyle(content[k], null).height);
        if (this.flag2) {
          if (st >= ot - H / 2 && st + H / 2 < ot + h) {
            this.num = k;
          }
        }
      }
    }
  }
};
</script>
<style scoped>
h2 {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: coral;
}
.nav {
  position: fixed;
  top: 50%;

  left: 10px;
  width: 100px;
  z-index: 100;
}
.nav li {
  line-height: 30px;
  text-align: center;
  cursor: pointer;
  background: purple;
  color: white;
}
.nav .con {
  color: coral;
  font-weight: bolder;
}
</style>